@import "../../../assets/styles/scss/utilities/functions";
@import "../../../assets/styles/scss/utilities/variables";
@import "../../../assets/styles/scss/utilities/mixins";
@import "../../../assets/styles/scss/utilities/default-variables";

.cards-default-card {
	opacity: 1;
	position: relative;
	border-radius: 2px;
	overflow: hidden;
	display: flex !important;
	width: 100%;
	border: none;
	margin-block-start: 0px !important;
	box-shadow: 0px 0px 40px 0px rgba(0, 0, 0, 0.15);
	align-items: center;
	flex-direction: row-reverse;
	transition: all .2s;
	gap: 50px;
	padding: 30px;

	@include media-breakpoint-up(sm) {
		width: 100%;
	}

	@include media-breakpoint-up(md) {
		width: calc(50% - 20px);
		gap: 100px;
		padding: 50px 40px;
	}

	@include media-breakpoint-up(xl) {
		width: calc(50% - 15px);
	}

	&__container-group {
		display: flex;
		flex-wrap: wrap;
		gap: 30px;
	}

	&::before {
		content: "";
		position: relative;
		width: 14px;
		height: 17px;
		background-image: url('data:image/svg+xml,<svg width="14" height="17" viewBox="0 0 14 17" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M13.7071 9.20711C14.0976 8.81658 14.0976 8.18342 13.7071 7.79289L7.34315 1.42893C6.95262 1.03841 6.31946 1.03841 5.92893 1.42893C5.53841 1.81946 5.53841 2.45262 5.92893 2.84315L11.5858 8.5L5.92893 14.1569C5.53841 14.5474 5.53841 15.1805 5.92893 15.5711C6.31946 15.9616 6.95262 15.9616 7.34315 15.5711L13.7071 9.20711ZM-8.74228e-08 9.5L13 9.5L13 7.5L8.74228e-08 7.5L-8.74228e-08 9.5Z" fill="%23210044"/></svg>');
		flex-shrink: 0;
	}

	&__container {
		display: flex;
		flex-wrap: wrap;
		flex-direction: column;
		padding: 0px;
		margin-top: 0px;
		width: 100%;
		height: 100%;
		gap: 12px;
		justify-content: flex-end;

		p {
			color: $ui-foreground-primary-text-body-text;
			transition: all .2s;
		}

		.wp-block-heading {
			@include stretched-link;
			margin-top: 0px;
			margin-bottom: 0px;
			transition: all .2s;
			text-decoration: none;
			font-weight: 700;
			color: var(--card-heading-default-color);
			transition: all .2s;
		}
	}

	@include media-breakpoint-up(xl) {
		&:hover {
			background-color: var(--card-default-background-color) !important;

			&::before {
				background-image: url('data:image/svg+xml,<svg width="14" height="16" viewBox="0 0 14 16" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M13.7071 8.70711C14.0976 8.31658 14.0976 7.68342 13.7071 7.29289L7.34315 0.928933C6.95262 0.538408 6.31946 0.538408 5.92893 0.928933C5.53841 1.31946 5.53841 1.95262 5.92893 2.34315L11.5858 8L5.92893 13.6569C5.53841 14.0474 5.53841 14.6805 5.92893 15.0711C6.31946 15.4616 6.95262 15.4616 7.34315 15.0711L13.7071 8.70711ZM-8.74228e-08 9L13 9L13 7L8.74228e-08 7L-8.74228e-08 9Z" fill="%23FBF9F8"/></svg>');
			}

			p {
				color: var(--card-default-color-hover);
			}

			.wp-block-heading {
				color: var(--card-default-color-hover) !important;
				text-decoration: none;
			}
		}
	}
}